import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

interface Points {
  active: boolean;
  points: number;
}

@Injectable({ providedIn: 'root' })
export class PointsService {
  private pointsSource = new BehaviorSubject<Points>({
    active: false,
    points: 0,
  });
  currentPoints = this.pointsSource.asObservable();
  timeout;

  constructor() {}

  changePoints(points: number) {
    if (this.pointsSource.value.active) {
      clearTimeout(this.timeout);

      this.pointsSource.next({
        active: true,
        points,
      });
    } else {
      this.pointsSource.next({
        active: true,
        points,
      });
    }

    this.timeout = setTimeout(() => {
      this.pointsSource.next({
        active: false,
        points,
      });
    }, 3000);
  }
}
