import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { fadeIn, fadeOut } from './app.animations';
import { Languages } from './shared/enums/languages.enum';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [fadeIn, fadeOut],
})
export class AppComponent implements OnInit {
  title = 'onelity-quiz-app';
  startPage = false;
  startQuizPanel = false;
  language = 'el';

  constructor(
    private domSanitizer: DomSanitizer,
    private matIconRegistry: MatIconRegistry,
    public translate: TranslateService
  ) {
    this.setFlagIcons();
  }

  useLanguage(language: Languages): void {
    this.translate.use(language);
  }

  ngOnInit() {
    setTimeout(() => {
      this.startPage = true;
    }, 2000);
  }

  startQuiz() {
    this.startPage = false;

    setTimeout(() => {
      this.startQuizPanel = true;
    }, 1000);
  }

  private setFlagIcons() {
    let listOfIcons: string[] = [];
    listOfIcons = [Languages.el, Languages.en];
    listOfIcons.forEach((icon) => {
      this.matIconRegistry.addSvgIcon(
        icon,
        this.domSanitizer.bypassSecurityTrustResourceUrl(
          `assets/flags/${icon}.svg`
        )
      );
    });
  }
}
