import {
  trigger,
  transition,
  query,
  style,
  stagger,
  animate,
  group,
  AnimationMetadata,
  keyframes,
  sequence,
} from '@angular/animations';

const enterTransition = transition(':enter', [
  style({
    opacity: 0,
  }),
  animate(
    '1s ease-in',
    style({
      opacity: 1,
    })
  ),
]);

const leaveTrans = transition(':leave', [
  style({
    opacity: 1,
  }),
  animate(
    '1s ease-out',
    style({
      opacity: 0,
    })
  ),
]);

export const listAnimation = trigger('listAnimation', [
  transition('* <=> *', [
    query(
      ':enter',
      [
        style({ opacity: 0 }),
        stagger('60ms', animate('600ms ease-out', style({ opacity: 1 }))),
      ],
      { optional: true }
    ),
    query(':leave', animate('300ms', style({ opacity: 0 })), {
      optional: true,
    }),
  ]),
]);

export const animSlider = trigger('animSlider', [
  transition(':increment', [
    query(':enter, :leave', style({ position: 'fixed', width: '{{width}}' }), {
      optional: true,
    }),
    group([
      query(
        ':enter',
        [
          style({ transform: 'translateX(10%)' }),
          animate('.3s ease-out', style({ transform: 'translateX(0%)' })),
        ],
        {
          optional: true,
        }
      ),
      query(
        ':leave',
        [
          style({ transform: 'translateX(0%)' }),
          animate(
            '.4s ease-out',
            style({ transform: 'translateX(-10%)', opacity: 0 })
          ),
        ],
        {
          optional: true,
        }
      ),
    ]),
  ]),
  transition(':decrement', [
    query(':enter, :leave', style({ position: 'fixed', width: '{{width}}' }), {
      optional: true,
    }),
    group([
      query(
        ':enter',
        [
          style({ transform: 'translateX(-10%)' }),
          animate('.3s ease-out', style({ transform: 'translateX(0%)' })),
        ],
        {
          optional: true,
        }
      ),
      query(
        ':leave',
        [
          style({ transform: 'translateX(0%)' }),
          animate(
            '.4s ease-out',
            style({ transform: 'translateX(10%)', opacity: 0 })
          ),
        ],
        {
          optional: true,
        }
      ),
    ]),
  ]),
]);

export const slideInOut = trigger('slideInOut', [
  transition(':enter', [
    style({ transform: 'translateY(-100%)', zIndex: '1' }),
    animate(
      '300ms ease-in',
      style({ transform: 'translateY(0%)', zIndex: '1' })
    ),
  ]),
  transition(':leave', [
    style({ zIndex: '1' }),
    animate('300ms ease-in', style({ transform: 'translateY(-100%)' })),
  ]),
]);

export const showAnimation: AnimationMetadata = sequence([
  query(
    'button',
    animate(
      '500ms ease-out',
      keyframes([
        style({ visibility: 'visible', offset: 0 }),
        style({ transform: 'scale(0.2)', offset: 0.2 }),
        style({ transform: 'scale(0.6)', offset: 0.4 }),
        style({ transform: 'scale(1)', offset: 0.5 }),
        style({ backgroundColor: '#673ab7', offset: 1 }),
      ])
    )
  ),
  query('.arrow', animate('100ms', keyframes([style({ opacity: '1' })]))),
]);

export const hideAnimation: AnimationMetadata = sequence([
  query(
    '.arrow',
    animate(
      '100ms',
      keyframes([
        style({ opacity: '0.8', offset: 0 }),
        style({ opacity: '0', offset: 1 }),
      ])
    )
  ),
  query(
    'button',
    animate(
      '100ms ease-in',
      keyframes([
        style({ transform: 'scale(0.4)', offset: 0.8 }),
        style({ transform: 'scale(0)', offset: 0.9 }),
        style({ visibility: 'hidden', offset: 1 }),
      ])
    )
  ),
]);

export const fadeIn = trigger('fadeIn', [enterTransition]);
export const fadeOut = trigger('fadeOut', [leaveTrans]);
