import { Component, HostListener, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { animSlider, fadeIn, fadeOut, listAnimation } from '../app.animations';
import { PointsService } from '../services/points.service';
import {
  faChevronLeft,
  faChevronRight,
} from '@fortawesome/free-solid-svg-icons';
import Utils from '../shared/utils/utils';
import { Constants } from '../shared/constants/constants';
import Question from '../shared/model/question';

@Component({
  selector: 'app-quiz-panel',
  templateUrl: './quiz-panel.component.html',
  styleUrls: ['./quiz-panel.component.scss'],
  animations: [animSlider, listAnimation, fadeIn, fadeOut],
})
export class QuizPanelComponent implements OnInit {
  @HostListener('window:resize', ['$event'])
  onResize() {
    this.screenWidth = window.innerWidth;
  }

  screenWidth: number;
  faChevronLeft = faChevronLeft;
  faChevronRight = faChevronRight;
  numberOfCorrectAnswers = 0;
  numberOfWrongAnswers = 0;
  counter: number = 0;
  showOptions = false;
  form: FormGroup;
  percentMatching = 70;
  points: number = 0;
  questions: Question[] = Constants.QUESTIONS;

  constructor(fb: FormBuilder, private pointsService: PointsService) {
    this.form = fb.group({
      first: new FormArray([]),
      second: new FormArray([]),
      third: new FormArray([]),
      fourth: new FormArray([]),
    });

    this.onResize();
  }

  ngOnInit(): void {
    setTimeout(() => {
      this.showOptions = true;
    }, 500);
  }

  onSwipe(evt) {
    const x =
      Math.abs(evt.deltaX) > 40 ? (evt.deltaX > 0 ? 'right' : 'left') : '';

    x === 'left' ? this.onNext() : this.onPrevious();
  }

  calculatePoints() {
    const firstArray: FormArray = this.form.get('first') as FormArray;
    const secondArray: FormArray = this.form.get('second') as FormArray;
    const thirdArray: FormArray = this.form.get('third') as FormArray;
    const fourthArray: FormArray = this.form.get('fourth') as FormArray;

    const { numberOfCorrectAnswers, numberOfWrongAnswers, points } =
      Utils.points(
        firstArray,
        secondArray,
        thirdArray,
        fourthArray,
        this.questions
      );

    this.numberOfCorrectAnswers = numberOfCorrectAnswers;
    this.numberOfWrongAnswers = numberOfWrongAnswers;

    this.pointsService.changePoints(points);
  }

  onCheckChange(item, formName) {
    const currentFormArray: FormArray = this.form.get(formName) as FormArray;
    currentFormArray.reset();
    currentFormArray.push(new FormControl(item.option));

    this.calculatePoints();
  }

  onNext() {
    if (this.counter != this.questions.length) {
      this.counter++;
      this.showOptions = false;

      setTimeout(() => {
        this.showOptions = true;
      }, 500);
    }

    if (this.counter === this.questions.length) {
      this.calculateMatching();
    }
  }

  isMobile() {
    return this.screenWidth <= 800;
  }

  calculateMatching() {
    switch (this.numberOfCorrectAnswers) {
      case 0: {
        this.percentMatching = 70;
        break;
      }
      case 1: {
        this.percentMatching = 80;
        break;
      }
      case 2: {
        this.percentMatching = 90;
        break;
      }
      case 3: {
        this.percentMatching = 95;
        break;
      }
      case 4: {
        this.percentMatching = 100;
        break;
      }
    }
  }

  isCorrectAnswer(item, question) {
    return (
      this.questions[this.counter].options[question.answer].option ===
      item.option
    );
  }

  isWrongAnswer(item, question) {
    return (
      this.form.get(question.formName).value &&
      this.questions[this.counter].options[question.answer].option !==
        item.option
    );
  }

  checkIfChecked(item, formName) {
    const formArray: FormArray = this.form.get(formName) as FormArray;
    return formArray.value.indexOf(item.option) !== -1;
  }

  onPrevious() {
    if (this.counter > 0) {
      this.counter--;

      this.showOptions = false;

      setTimeout(() => {
        this.showOptions = true;
      }, 500);
    }
  }
}
