import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

interface KeyValue {
  key: string;
  value: string;
}

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss'],
})
export class ContactComponent implements OnInit {
  //onelity google form
  url =
    'https://docs.google.com/forms/u/0/d/e/1FAIpQLSeRtvAaMxQlozPNOnBxLl4Js8pu7XJii_Jbqlc7HZXax3F0kA/formResponse';

  formSubmitted = false;
  submitted = false;
  fieldMapping = {
    name: 'entry.2005620554',
    email: 'entry.1045781291',
    comments: 'entry.839337160',
  };

  formData = this.fb.group({
    name: ['', Validators.required],
    email: ['', [Validators.required, Validators.email]],
    comments: ['', Validators.required],
  });

  constructor(private fb: FormBuilder, private http: HttpClient) {}

  ngOnInit(): void {}

  get f() {
    return this.formData.controls;
  }

  save() {
    this.submitted = true;
    if (this.formData.invalid) {
      return;
    }

    const rawValue = this.formData.getRawValue();
    let body = new HttpParams();

    Object.entries(rawValue).forEach((entry) => {
      const [key, value] = entry;
      body = body.append(this.fieldMapping[key], value as string);
    });

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/x-www-form-urlencoded',
      }),
    };

    this.http.post(this.url, body, httpOptions).subscribe(
      () => {
        this.formSubmitted = true;
        this.onReset();
      },
      (err) => {
        this.formSubmitted = true;
        this.onReset();
      }
    );
  }

  onReset() {
    this.submitted = false;
    this.formData.reset();
  }
}
